.hero {
    width: 100vw;
    height: 76vh;
    background-image:  linear-gradient( rgba(0, 0, 0, 0.220), rgba(0, 0, 0, 0.220) ),url("../img/hero/de40c945c56454ad3815316b2f9cd036.jpg");
    background-repeat: no-repeat;
    background-size:auto;
    color: #fff;
  
    .conteiner {
      height: 100%;
      width: 80%;
      margin-left: 10%;
      display: flex;
      flex-direction: column;
      min-width: 300px;
  
      .header {
        display: flex;
        align-items: center;
        gap: 10px;
        padding: 2% 0;
        
        .logo {
          h3 {
            font-size: 34px;
          }
        }
  
        .nav_header {
          margin-left: auto;
          font-size: 20px;
          
          ul {
            display: flex;
            list-style: none;
            gap: 10px;
            
            li {
              display: inline;
            }
          }
        }
  
        .action {
          ul {
            display: flex;
            list-style: none;
            gap: 10px;
            
            li {
              display: inline;
            }
          }
        }
      }
  
      .hero-main {
        height: auto;
        padding: 3% 0;
        display: flex;
        flex-direction: column;
        align-items: center;
        gap: 30px;
        text-align: center;
  
        .main_title {
          font-size: 58px;
        }
  
        .main_info {
          padding: 3% 0;
          font-size: 20px;
        }
  
        .main_button {
          padding: 2% 0;
          display: flex;
          gap: 30px;
          
          .hero-main-button {
            height: 60px;
            width: 200px;
            border-radius: 30px;
            font-size: larger;
            background-color: #ffffff13;
            border: solid 1px #ffffff;
            color: #fff;
            transition: background-color 0.25s ease-out, transform 0.25s ease-out;
          }
  
          .hero-main-button:hover {
            background-color: rgb(255, 174, 0);
            color: #fff;
            transform: scale(1.05);
          }
        }
      }
    }
  }
  
  a {
    color: #fff;
    text-decoration: none;
    position: relative;
  
    &::after {
      content: "";
      position: absolute;
      left: 0;
      bottom: -2px;
      width: 100%;
      height: 3px;
      background-color: rgb(255, 174, 0);
      transform: scaleX(0);
      transform-origin: bottom right;
      transition: transform 0.25s ease-out;
    }
  
    &:hover::after {
      transform: scaleX(1);
      transform-origin: bottom left;
    }
  }
  
  @keyframes button-animation {
    from {
      transform: translateY(0);
    }
    to {
      transform: translateY(-10px);
    }
  }
  